body {
   margin          : 0;
   padding         : 0;
   background-color: #eff3f7;
   height          : 100%;
}

.container {
   padding: 20px;
}

html,
body,
#root,
.container {
   height: 100%;
}

.loader {
   width          : 100%;
   height         : 100%;
   display        : flex;
   justify-content: center;
   align-items    : center;
}

.movies {
   display              : grid;
   grid-template-columns: 1fr 1fr;
   grid-gap             : 70px 70px;
   padding              : 20px;

}

img {
   margin-top  : -20px;
   margin-left : 20px;
   margin-right: 10px;
   box-shadow  : 3px 3px 7px rgb(158, 158, 158),
      -3px 3px 7px rgb(158, 158, 158),
      3px -3px 7px rgb(158, 158, 158),
      -3px -3px 7px rgb(158, 158, 158);
   box-sizing   : border-box;
   border-radius: 15px;
   border       : 1px solid #999999;

}

.movie {
   display              : grid;
   grid-template-columns: 1fr 2fr;
   box-shadow           : 3px 3px 5px #999999;
   border-radius        : 10px;
}

ul.genres {
   padding-left: 5px;

}

ul.genres>li.genres__genre {
   display: inline;
}

ul.genres>li.genres__genre::after {
   content: " / ";
}

ul.genres>li:last-child.genres__genre::after {
   content: "";
}




p.movie__summary {
   /* border    : 1px solid #000; */
   height       : 150px;
   overflow     : hidden;
   text-overflow: ellipsis;
   font-size    : 12px;
   padding      : 10px;
}